import { Fragment } from "react";
import { useSelector } from "react-redux";
import FilterButton from "../../shared/FormElements/FilterButton";

const SubCategoryFilter = (props) => {
  let filters = useSelector((state) => state.products.filters);
  const addCategoryFilterHandler = (event) => {
    props.onSaveFilter({ name: "subCategories", value: event.target.value });
  };

  return (
    (filters.categories.includes("Vadszfegyverek") || filters.categories.includes("Optikk")) && (
      <Fragment>
        <div className="text-sm font-extrabold mt-4">
          <span className="bg-clip-text text-transparent bg-customBlue">
            Alkategrik
          </span>
        </div>
        {filters.categories.includes("Vadszfegyverek") && (
          <div>
            <FilterButton
              value="Golys puska"
              title="golys puska"
              activated={filters?.subCategories.includes("Golys puska")}
              onSaveData={addCategoryFilterHandler}
            />
            <FilterButton
              value="Srtes puska"
              title="srtes puska"
              activated={filters?.subCategories.includes("Srtes puska")}
              onSaveData={addCategoryFilterHandler}
            />
            <FilterButton
              value="Vegyescsv puska"
              title="Vegyescsv puska"
              activated={filters?.subCategories.includes("Vegyescsv puska")}
              onSaveData={addCategoryFilterHandler}
            />
            <FilterButton
              value="Maroklfegyver"
              title="Maroklfegyver"
              activated={filters?.subCategories.includes("Maroklfegyver")}
              onSaveData={addCategoryFilterHandler}
            />
            <FilterButton
              value="Egyb fegyverek"
              title="egyb fegyverek"
              activated={filters?.subCategories.includes("Egyb fegyverek")}
              onSaveData={addCategoryFilterHandler}
            />
          </div>
        )}

        {filters.categories.includes("Optikk") && (
          <div>
            <FilterButton
              value="Tvcsvek"
              title="tvcsvek"
              activated={filters?.subCategories.includes("Tvcsvek")}
              onSaveData={addCategoryFilterHandler}
            />
            <FilterButton
              value="jjellt tvcsvek"
              title="jjellt tvcs"
              activated={filters?.subCategories.includes("jjellt tvcsvek")}
              onSaveData={addCategoryFilterHandler}
            />
            <FilterButton
              value="Hkamerk"
              title="hkamerk"
              activated={filters?.subCategories.includes("Hkamerk")}
              onSaveData={addCategoryFilterHandler}
            />
            <FilterButton
              value="Vadkamera"
              title="vadkamera"
              activated={filters?.subCategories.includes("Vadkamera")}
              onSaveData={addCategoryFilterHandler}
            />
          </div>
        )}
      </Fragment>
    )
  );
};

export default SubCategoryFilter;
