import { Fragment } from "react";
import { useSelector } from "react-redux";
import FilterButton from "../../shared/FormElements/FilterButton";

const CategoryFilter = (props) => {
  let filters = useSelector((state) => state.products.filters.categories);
  const addCategoryFilterHandler = (event) => {
    props.onSaveFilter({ name: "categories", value: event.target.value });
  };


  return (
    <Fragment>
      <div className="text-sm font-extrabold">
        <span className="bg-clip-text text-transparent bg-customBlue">
          Kategrik
        </span>
      </div>
      <div>
        <FilterButton
          value="Vadszfegyverek"
          title="vadszfegyverek"
          activated={filters.includes("Vadszfegyverek")}
          onSaveData={addCategoryFilterHandler}
        />
        <FilterButton
          value="Lszerek"
          title="lszerek"
          activated={filters?.includes("Lszerek")}
          onSaveData={addCategoryFilterHandler}
        />
        <FilterButton
          value="Optikk"
          title="optikk"
          activated={filters?.includes("Optikk")}
          onSaveData={addCategoryFilterHandler}
        />
        <FilterButton
          value="Ruhzat"
          title="ruhzat"
          activated={filters?.includes("Ruhzat")}
          onSaveData={addCategoryFilterHandler}
        />
        <FilterButton
          value="Vadszkutyk"
          title="vadszkutyk"
          activated={filters?.includes("Vadszkutyk")}
          onSaveData={addCategoryFilterHandler}
        />
        <FilterButton
          value="Jrmvek"
          title="jrmvek"
          activated={filters?.includes("Jrmvek")}
          onSaveData={addCategoryFilterHandler}
        />
        <FilterButton
          value="Ingatlanok"
          title="ingatlanok"
          activated={filters?.includes("Ingatlanok")}
          onSaveData={addCategoryFilterHandler}
        />
        <FilterButton
          value="Kellkek"
          title="kellkek"
          activated={filters?.includes("Kellkek")}
          onSaveData={addCategoryFilterHandler}
        />
      </div>
    </Fragment>
  );
};

export default CategoryFilter;
